
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app>img{
  display: block;
  width: 100px;
  margin: 0 auto;
}
ul,li{
  list-style: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.nav li{
  margin-top: 5px;
  text-align: center;
}
a {
  color: #42b983;
}



